@import url('https://fonts.googleapis.com/css2?family=DM+Mono:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@700&family=DM+Mono:wght@300&display=swap');
*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}
#drum-machine{
  width:100vw;
  height:100vh;
  background-color:#d63031;
  display:flex;
  align-items:center;
  justify-content:center;
}
.drum-pad {
  border: 1px solid;
  width:100px;
  height:100px;
  margin:10px;
  display:flex;
  align-items:center;
  justify-content:center;
  font-size:40px;
  font-family:DM Mono , Monospace;
  border-radius:5px;
  box-shadow: 0 0 10px 4px black;
  cursor:pointer;
  background-color:#fdcb6e;
  
}
#display{
  display:flex;
  flex-wrap:wrap;
  width:410px;
  background-color:#fff;
  padding:20px;
  border:5px solid #fdcb6e;
  border-radius:30px;
  
}
#display h1{
  display:block;
  width:100%;
  text-align:center;
  padding-bottom:1.2rem;
  font-family:Balsamiq Sans,monospace;
    
}
@media only screen and (max-width: 600px){
  .drum-pad {
    width: 50px;
    height: 50px ;
    margin: 5px;
    font-size:20px;
  }
  #display{
    padding:10px;
    width:210px;
  }
}